body {
  display: flex;
  justify-content: center;
  font-weight: bold;
  font-family: "Comic Sans MS", cursive, sans-serif;
  color: #4d4d4d;
  background-color: #ededed;
}

div {
  display: flex;
  flex-flow: column;
  align-items: center;
  align-content: space-around;
  background-color: #b8cff5;
  width: 750px;
  padding: 10px;
  border-radius: 25px;
}

form {
  display: flex;
  flex-flow: column;
  align-items: center;
}

label {
  margin: 0 0 20px 0;
  font-size: 20px;
}

h1 {
  margin: 20px 0 40px 0;
  font-size: 40px;
  color: #2b2b2b;
}

button {
  margin: 15px 0 0 0;
  font-weight: bold;
  font-family: "Comic Sans MS", cursive, sans-serif;
  color: #f2f7f7;
  font-size: 20px;  
  border-radius: 8px;
  background-color: #849bc4;
}

p {
  margin: 25px 0 35px 0;
  font-size: 30px;
  color: #2b2b2b;
}

span {
  color: red;
}

input{
  box-shadow: 2px 2px 5px grey;
  border: 1px solid #8c8c8c;
  border-radius: 4px;
  box-sizing: border-box;
}

.checkbox {
  background-color: white;
}